<template>
  <div class="filter-container">
    <div class="filtered-companies" v-for="(company, indx) in companies" :key="indx">
      <h4>{{ company.name }}</h4>
      <h6>{{ company.kategorie }}</h6>
      <table class="table">
        <tr>
          <td>
            <font-awesome-icon icon="envelope" />
          </td>
          <td>{{ company.email }}</td>
        </tr>
        <tr>
          <td>
            <font-awesome-icon icon="phone" />
          </td>
          <td>{{ company.tel }}</td>
        </tr>
        <tr>
          <td>
            <font-awesome-icon icon="map-marker-alt" />
          </td>
          <td>
            {{ company.straße }} {{ company.hnummer }}, {{ company.plz }}
            {{ company.stadt }}
          </td>
          <td></td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Search",
  data() {
    return {
      companies: "",
      isHidden: true
    };
  },
  mounted() {
    axios
      .get("http://localhost:5000/companies")
      .then(response => {
        this.companies = response.data;
      })
      .catch(error => {
        console.warn("ERROR");
      });
  }
};
</script>

<style lang="scss">
.filter-container {
  padding-top: 15%;
}

.filtered-companies {
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.493);
}

.table td {
  color: #000;
}
</style>
